import React from "react"

import {HeaderMenuTestsCont} from "HeaderMenuTestsCont.js";
import {HeaderMenuTests} from "HeaderMenuTests.js";
import {ListTestsWrapper} from "ListTestsWrapper.js";
import {HeaderMenuWords} from "HeaderMenuWords.js";
import {ListWordsWrapper} from "ListWordsWrapper.js";
import {HeaderMenuListen} from "HeaderMenuListen.js";
import {ListListenWrapper} from "ListListenWrapper.js";
import {HeaderMenuPhrases} from "HeaderMenuPhrases.js";
import {ListPhrasesWrapper} from "ListPhrasesWrapper.js";
import {HeaderMenuJokes} from "HeaderMenuJokes.js";
import {ListJokesWrapper} from "ListJokesWrapper.js";


export function HeaderBgrTest() {
    const london_style = {
      position: 'relative',
      backgroundImage: 'url(London.jpg)',
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      backgroundPositionX: '50%',
      color: 'white'
    }

    return ( 
      <div style={london_style}> 
        <p>MainReducer</p>      
        <div className="header_header">
          <div id="header_menu_tests_cont_id" className="header_menu_tests_cont">
            <HeaderMenuTestsCont f1={HeaderMenuTests} f2={ListTestsWrapper} />
          </div>
          <div id="header_menu_tests_cont_id" className="header_menu_tests_cont">
            <HeaderMenuTestsCont f1={HeaderMenuWords} f2={ListWordsWrapper} />
          </div>
          <div id="header_menu_tests_cont_id" className="header_menu_tests_cont">
            <HeaderMenuTestsCont f1={HeaderMenuListen} f2={ListListenWrapper} />
          </div>
          <div id="header_menu_tests_cont_id" className="header_menu_tests_cont">
            <HeaderMenuTestsCont f1={HeaderMenuPhrases} f2={ListPhrasesWrapper} />
          </div>      
          <div id="header_menu_tests_cont_id" className="header_menu_tests_cont">
            <HeaderMenuTestsCont f1={HeaderMenuJokes} f2={ListJokesWrapper} />
          </div>            
        </div>    
        <div id="header_text_1">
          <b>English Learning Tests</b>
        </div>
      </div>
    )

}//end of HeaderBgrTest----------


